.App {
  text-align: center;
  min-height: 100vh;
  background-color: var(--tg-theme-bg-color, #ffffff);
  color: var(--tg-theme-text-color, #000000);
}

.App.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  padding-top: calc(16px + var(--safe-area-inset-top));
}

h1 {
  margin-bottom: 20px;
  font-size: 1.8rem;
}

.user-info {
  background-color: var(--tg-theme-secondary-bg-color, #f5f5f5);
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 20px;
  width: 90%;
  max-width: 400px;
}

.user-info p {
  margin: 5px 0;
}

.buttons {
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: 90%;
  max-width: 400px;
  margin-top: 20px;
}

.app-button {
  background-color: var(--tg-theme-button-color, #50a8eb);
  color: var(--tg-theme-button-text-color, #ffffff);
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s;
}

.app-button:hover {
  opacity: 0.9;
}

.app-button:active {
  opacity: 0.7;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
